.masthead {
    background: repeat-x url('./img/masthead-bg.png');
    margin: 0 -24px;
    padding: 0 16px;
    height: 35vh;
}

.masthead-logo {
    max-width: 100%;
    max-height: 35vh;
    opacity: 0;
    transition: opacity 2s;
}
.masthead-logo--loaded {
    opacity: 1;
}

@media (orientation: landscape) {
    .masthead {
        height: 50vh;
    }
    .masthead-logo {
        max-height: 50vh;
    }
}

a {
    color: #07aeef;
}

.section-icon {
    width: 80px;
}

.video {
    width: 100%;
    opacity: .3;
}

button.cta {
    font-size: 30px;
    animation: pulsate 8s infinite both;
}

button.cta:hover {
    background: #fff;
    color: #490ec0;
}

.bg-1 {
    background: repeat url('./img/bg-1.png');
}
.bg-2 {
    background: center bottom repeat-x url('./img/bg-2.png');
}
.bg-3 {
    background-color: #030008;
    opacity: 1;
    background-image:  repeating-radial-gradient( circle at 0 0, transparent 0, #030008 18px ), repeating-linear-gradient( #0b0b1255, #080810 );
}
.sunrise {
    display: block;
    margin: 100px auto 0;
    width: 100%;
    max-width: 400px;
}
.gr-left {
    z-index: 3;
    position: absolute;
    left: 0;
    height: 100%;
    width: 200px;
    background: repeat-y url('./img/gr-left.png');
}
.gr-right {
    z-index: 3;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 200px;
    background: repeat-y url('./img/gr-right.png');
}

@keyframes pulsate {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(0.9);
    }
    100% {
        transform: scale(1);
    }
}