.events-list__message {
    display: flex;
    align-items: center;
    flex: 1 0 100%;
    padding: 20px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    font: 14px/1.5 'Roboto', sans-serif;
}

.events-list__message:last-child {
    border: none;
}

.events-list__number {
    margin: 0 10px 0 0;
    color: rgba(255, 255, 255, 0.5);
    font-size: 11px;
    line-height: 1;
}
