html {
    background: #000;
}

body {
    margin: 0;
    /* font-family: 'Roboto', sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* background: #1c0f3a;
    background: linear-gradient(90deg, #000 0%, #1c0f3a 50%, #000 100%); */
}

button {
    outline: none;
    cursor: pointer;
    user-select: none;
}

a,
div,
input,
button {
    box-sizing: border-box;
}

#root {
    position: relative;
    margin: auto;
}

.app {
    min-height: 100vh;
    background: #000;
    color: #fff;
    font-size: 15px;
}

.button-1 {
    width: 100%;
    display: block;
    margin: 20px 0;
    padding: 0 10px;
    color: #fff;
    line-height: 46px;
    background: #490ec0;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-radius: 20px;
    border: none;
    font-family: "Nunito", "Helvetica", "Arial", sans-serif;
    text-transform: uppercase;
}
.button-1--alt {
    background: #161728;
}

.row-1 {
    margin: 0;
    padding: 10px;
    background: #0f0623;
    border-bottom: 1px solid #21173a;
}

.text-align-right {
    text-align: right;
}

.display-none {
    display: none;
}
