.launch-paused-overlay {
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    text-align: center;
    font-size: 75px;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.2);
    line-height: 100vh;
    user-select: none;
}

.debug-buttons {
    position: absolute;
    left: 0;
    width: 80px;
    top: 25%;
}