.radio-group {
    display: flex;
}

.radio-group__input {
    display: none;
}

.radio-group__item {
    margin: 10px 10px 10px 0;
    flex: 1;
}
.radio-group__item:last-child {
    margin: 10px 0;
}

.radio-group__input-label {
    display: block;
    color: #fff;
    background: #161728;
    font-size: 16px;
    line-height: 46px;
    text-align: center;
    border-radius: 20px;
    cursor: pointer;
    font-family: "Nunito", "Helvetica", "Arial", sans-serif;
    text-transform: uppercase;
}

.radio-group__input:checked + .radio-group__input-label {
    background: #490ec0;
}
