.page {
    max-width: 640px;
    min-height: 100vh;
    margin: auto;
    padding: 40px 40px 20px;
}

.page h1 {
    text-align: center;
}

.page__logo {
    position: relative;
    z-index: 2;
    max-width: 320px;
    margin: 0 auto 50px;
    text-align: center;
    filter: drop-shadow(0 0 10px #000);
}

.page__logo_img {
    max-width: 100%;
}

.page__content {
    position: relative;
    z-index: 2;
    max-width: 640px;
    margin: auto;
    padding: 0 0 440px;
}

@media (max-width: 480px) {
    .page__content {
        padding: 0 0 300px;
    }
}

.page__bg--top {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background: center no-repeat url('./img/top.png');
    background-size: cover;
    /* filter: brightness(70%) hue-rotate(55deg) grayscale(10%); */
}

.page__bg--bottom {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: flex;
    justify-content: center;
}

.page__bg--bottom_img {
    width: 100%;
    max-width: 640px;
}

.page .button-1 {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
