.row {
    display: flex;
    /* flex-direction: column; */
    padding: 0;
    width: 100%;
}
.row.row-wrap {
    flex-wrap: wrap;
}

.row.row-top {
    align-items: flex-start;
}

.row.row-bottom {
    align-items: flex-end;
}

.row.row-center {
    align-items: center;
}

.row.row-stretch {
    align-items: stretch;
}

.row.row-baseline {
    align-items: baseline;
}
.row .col {
    display: block;
    flex: 1 1 auto;
    margin-left: 0;
    max-width: 100%;
    /* width: 100%; */
}
.row .col.col-25 {
    flex: 0 0 25%;
    max-width: 25%;
}
.row .col.col-33 {
    flex: 0 0 33.333%;
    max-width: 33.333%;
}
.row .col.col-50 {
    flex: 0 0 50%;
    max-width: 50%;
}
.row .col.col-75 {
    flex: 0 0 75%;
    max-width: 75%;
}
.row .col.col-offset-25 {
    margin-left: 25%;
}
.row .col.col-offset-50 {
    margin-left: 50%;
}
.row .col.col-offset-75 {
    margin-left: 75%;
}
