.fleet-list__item {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 20px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    font: 14px/1.5 'Roboto', sans-serif;
    cursor: pointer;
    user-select: none;
}

.fleet-list__item:last-child {
    border: none;
}

.fleet-list__icon {
    width: 14px;
    height: 14px;
    margin: 0 10px 0 0;
}

.fleet-list__icon--ship {
    width: 12px;
    height: 12px;
    transform: rotate(-45deg) translateX(2px) translateY(2px);
}

.fleet-list__orb {
    width: 16px;
    height: 16px;
    margin: 0 10px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.4);
}
