.game-over-overlay {
    position: absolute;
    width: 100vw;
    bottom: 0;
    z-index: 3;
    text-align: center;
    color: #fff;
    transition: bottom 300ms, transform 300ms;
    transform: scaleY(0);
    transform-origin: center bottom;
}

.game-over-overlay--open {
    bottom: 115px;
    transform: scaleY(1);
}

@media (orientation: landscape) {
    .game-over-overlay--open {
        bottom: 100px;
        width: calc(100vw - 100px);
        right: 100px;
    }
}

.game-over-overlay-inner {
    padding: 0 15px 10px;
    background: rgba(0, 0, 0, 0.8);
    border: 1px solid #4d3cb1;
    border-radius: 20px;
    width: 90%;
    max-width: 640px;
    margin: auto;
}

.game-over-overlay-heading {
    margin: 15px 10px 10px;
    font-size: 23px;
    font-weight: bold;
}


.game-over-overlay .button-1 {
    margin: 0;
}
