.toolbar {
    display: flex;
    background: #490ec0;
    z-index: 10;
    position: fixed;
}

.toolbar .toolbar__menu-item,
.toolbar__button {
    display: flex;
    padding: 0;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin: 5px;
    color: #fff;
    font-size: 18px;
    background: none;
    border: none;
}

.toolbar .toolbar__menu-item {
    position: relative;
}

.toolbar__menu {
    background: rgba(18, 9, 77, 0.8);
    position: fixed;
}

.toolbar__menu-item .toolbar__icon {
    /* margin: 0 10px; */
}

.toolbar__menu-button-cost {
    color: #fff;
    /* position: absolute; */
    /* bottom: -1px; */
    text-align: center;
    font-size: 10px;
    font-weight: bold;
    white-space: nowrap;
    background: #490ec0;
    border-radius: 10px;
    padding: 1px 4px;
    margin: 0 0 0 4px;
}

.toolbar__menu-button-cost-icon {
    height: 8px;
    margin: 0 0 -1px 1px;
}

.toolbar__menu-label {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.toolbar__button--active {
    background: rgba(0, 0, 0, 0.4);
}
.toolbar__icon.toolbar__icon--hamburger {
    width: 26px;
}
.toolbar__icon--ship {
    transform: rotate(315deg) scale(.89);
    margin: -5px 0 0;
}
.toolbar__icon--towable {
    transform: rotate(45deg) scale(.89);
}

.toolbar__menu-item--disabled,
.toolbar__menu-item:disabled,
.toolbar__button--disabled,
.toolbar__button:disabled {
    opacity: 0.3;
}

.toolbar__icon {
    width: 22px;
}

@media (orientation: portrait) {
    .toolbar {
        flex-direction: row;
        width: 100vw;
        height: 50px;
        bottom: 0;
        justify-content: space-around;
        /* flex-direction: row-reverse; */
    }

    .toolbar__menu {
        bottom: 50px;
        position: fixed;
        width: 100vw;
        display: flex;
        justify-content: space-around;
    }

    @media (min-device-width: 768px) {
        .toolbar,
        .toolbar__menu {
            width: auto;
            left: 25%;
            right: 25%;
        }
    }
}

@media (orientation: landscape) {
    .toolbar {
        flex-direction: column;
        width: 50px;
        height: 100vh;
        right: 0;
        justify-content: space-evenly;
    }

    .toolbar .toolbar__menu-item {
        flex-wrap: wrap;
    }
    .toolbar__menu-button-cost {
        margin: 0;
    }

    .toolbar__menu {
        width: 50px;
        right: 50px;
        height: 100vh;
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    @media (min-device-width: 1024px) {
        .toolbar {
            margin: 25vh 0 0;
            height: auto;
        }

        .toolbar__menu {
            top: 25vh;
            height: 350px;
        }
    }
}
