.modal {
    position: absolute;
    z-index: 3;
    top: 15%;
    left: 0;
    right: 0;
    padding: 20px;
    overflow: hidden;
    opacity: 0;
    transition: opacity 300ms, transform 300ms;
    transform: scale(0);
    transform-origin: center;
    font-size: 13px;
}

.modal--open {
    opacity: 1;
    transform: scale(1);
}

.modal-inner {
    max-width: 640px;
    margin: auto;
    padding: 15px;
    background: rgba(0, 0, 0, 0.9);
    border: 1px solid #4d3cb1;
    border-radius: 6px;
    box-shadow: 0 0 13px rgba(0, 0, 0, 0.9);
}

.modal--open .modal-inner {
}

.modal-content {
    display: flex;
    flex-wrap: wrap;
    margin: 20px 0;
    padding: 0 20px 0 0;
    max-height: 220px;
    overflow-y: scroll;
}

.modal-content p {
    font-size: 14px;
    line-height: 1.4;
}

.modal-header__tabs {
    margin: 0 0 0 -3px;
}

.modal-header__tab {
    margin: 0 2px 0 0;
    border-radius: 20px;
    text-align: center;
    line-height: 42px;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
    user-select: none;
    background: rgba(255, 255, 255, 0.05);
}
.modal-header__tab--active {
    background: #242558;
}

.modal-header h2 {
    margin: 0;
}

.modal-header__icon {
    width: 21px;
}

.modal-heading {
    margin: 0;
    font-size: 20px;
}

.modal-value {
    font-size: 13px;
    font-weight: normal;
    text-transform: capitalize;
}

.modal-inline-img {
    height: 10px;
}

.modal-close-icon {
    color: #fff;
    font-size: 27px;
    margin: -2px 10px 0 0;
}

.button-1.modal-button {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (orientation: landscape) {
    .modal {
        right: 50px;
        top: 10%;
    }
}
