table {
    margin-bottom: 2.5rem;
}

table {
    border-spacing: 0;
    width: 100%;
}

td,
th {
    border-bottom: 0.1rem solid rgba(255, 255, 255, 0.1);
    padding: 0.8rem 1.5rem;
    text-align: left;
}

td:first-child,
th:first-child {
    padding-left: 0;
}

td:last-child,
th:last-child {
    padding-right: 0;
}
