.score {
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    display: flex;
    width: 100%;
    height: 24px;
    align-items: center;
    justify-content: space-evenly;
    background: rgba(0, 0, 0, 0.4);

}

/* @media (orientation: landscape) {
    .score {
        top: 53px;
        right: 54px;
    }
} */

.score__item {
    /* padding: 2px 0; */
    text-shadow: 0 0px 5px rgba(0, 0, 0, 0.02);
    font-weight: bold;
    color: #fff;
    cursor: pointer;
    user-select: none;
}

.score__item--resources {
    color: #77cf0f;
}

.score__icon {
    width: 11px;
    margin: 0 5px 0 0;
    vertical-align: middle;
}

.score__item--artifacts .score__icon {
    transform: rotate(-45deg) translateX(2px);
    margin: 1px 10px 0 0;
    width: 9px;
}
