@keyframes shrink {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}

@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes bounce {
    0%,
    100% {
        transform: scale(0);
    }
    50% {
        transform: scale(1);
    }
}

.page.splash .page__logo,
.page.splash .page__bg--bottom {
    display: none;
}

.page.splash .page__content {
    padding: 0;
}

.load-splash {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 4;
    background: #000;
    transition: opacity 1s;
}

.load-splash--hide {
    opacity: 0;
    animation: shrink 1ms linear forwards;
    animation-delay: 1s;
}

.load-splash__spinner {
    zoom: 2;
    margin: 15vh auto 20px;
    width: 40px;
    height: 40px;
    position: relative;
    text-align: center;
    animation: rotate 2s infinite linear;
}

.load-splash__label {
    text-align: center;
    font-weight: bold;
}

.load-splash__dot1,
.load-splash__dot2 {
    width: 60%;
    height: 60%;
    display: inline-block;
    position: absolute;
    top: 0;
    background-color: #fff;
    border-radius: 100%;
    animation: bounce 2s infinite ease-in-out;
}

.load-splash__dot2 {
    top: auto;
    bottom: 0;
    animation-delay: -1s;
}
