.atlas-list__item {
    display: flex;
    font: 14px/1.5 'Roboto', sans-serif;
    margin: 0 0 10px;
}

@media (max-width: 640px) {
    .atlas-list__item {
        flex: 0 1 48%;
    }
}

@media (min-width: 641px) {
    .atlas-list__item {
        flex: 0 1 33%;
    }
}

.atlas-list__description {
    margin: 5px 0 0;
    cursor: pointer;
}

.atlas-list__ships {
    display: flex;
}

.atlas-list__ships-item {
    color: #77cf0f;
    display: flex;
    align-items: center;
}

.atlas-list__ships-item:first-child {
    margin: 0 7px 0 0;
}

.atlas-list__item canvas {
    width: 60px;
    margin: 0 10px 0 0;
    cursor: pointer;
}

.atlas-list__icon {
    width: 14px;
    height: 14px;
    margin: 0 3px 0 0;
}

.atlas-list__icon--ship {
    width: 12px;
    height: 12px;
    transform: rotate(-45deg) translateX(2px) translateY(0);
}
