@keyframes last {
    0% {
        transform: scale(0);
    }
    5% {
        transform: scale(1);
    }
    95% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}

.event-log__message {
    position: fixed;
    z-index: 2;
    left: 0;
    right: 0;
    top: 24px;
    padding: 4px 8px;
    animation: last 5s linear forwards;
    transform-origin: left center;
    font: 15px/1 'Roboto', sans-serif;
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.98);
    background: rgba(0, 0, 0, 0.4);
}
